



















import {Component, Prop, Vue} from "vue-property-decorator"
import {IBooking, TrainingSessionType} from "@chaosinsight/yogipeople-portalclient"

@Component({ components: { } })
export default class BookingCountdown extends Vue {
	@Prop()
	private booking!: IBooking

	@Prop()
	private systemTime!: string

	private timeLeft: string | null = null
	private timeToStart!: number
	private listenCounter: number | null = null
	private timeDelta: number = new Date(this.systemTime).getTime() - Date.now()

	private confirmCancel: boolean | null = null

	private mounted(): void {
		this.countDown()
	}
	private destroyed(): void {
		if (this.listenCounter !== null) clearTimeout(this.listenCounter)
	}

	/**
	 * Training type name
	 */

	private getTrainingType(typeId: number): string {
		const type = TrainingSessionType[typeId]
		return type.charAt(0).toUpperCase() + type.slice(1)
	}

	/**
	 * Time countdown
	 */

	private countDown() {
		this.calcTime()
		if (this.listenCounter !== null && this.timeToStart <= -1000) {
			clearTimeout(this.listenCounter)
			this.$emit("startBookingSession", this.booking)
		} else this.listenCounter = window.setTimeout(() => this.countDown(), 1000)
	}

	private calcTime() {
		this.timeToStart = new Date(this.booking.TimeSlot.Start).getTime() - Date.now() - this.timeDelta
		const h = Math.floor((this.timeToStart / 1000 / 60 / 60))
		const m = Math.floor((this.timeToStart / 1000 / 60) % 60)
		const s = Math.floor((this.timeToStart / 1000) % 60)
		this.timeLeft = `${this.leadZero(h)}:${this.leadZero(m)}:${this.leadZero(s)}`
	}

	/**
	 * Cancel booking
	 */

	private cancelBooking(): void {

		if (this.confirmCancel) {
			this.$emit("cancelBooking", this.booking.Id)
			window.localStorage.setItem("cancelBooking", "yes") }

		this.confirmCancel = true
	}

	/**
	 * Lead zero
	 */

	private leadZero = (x: number): string => x < 10 ? x < 0 ? `00` :	`0${x}` : `${x}`
}
