

















import {Component, Prop, Vue} from "vue-property-decorator"
import Portal from "@/managers/portal"
import {IBooking, ITrainingSession} from "@chaosinsight/yogipeople-portalclient"
import MessageBar from "@/components/MessageBar.vue"
import BookingCountdown from "@/components/BookingCountdown.vue"
import NotificationMessage from "@/components/NotificationMessage.vue"
import i18n from "../i18n"
import { TranslateResult } from "vue-i18n"

@Component({ components: { MessageBar, BookingCountdown, NotificationMessage } })
export default class TrainerBookings extends Vue {
	@Prop({default: false})
	public listen!: boolean

	private bookings: IBooking[] = []

	private listenInterval = 5000
	private listenBooking: number | null = null
	private clientTimeDelta: number = 0

	private notificationMsg: string | null = null

	private daysInWeek: TranslateResult[] = [i18n.t("sunday"), i18n.t("monday"),
		i18n.t("tuesday"), i18n.t("wednesday"), i18n.t("thursday"),
		i18n.t("friday"), i18n.t("saturday")]

	private created(): void {
		Portal.client.call.system.Time()
			.response
			.then(res => this.clientTimeDelta = new Date(res.Time).getTime() - Date.now() )
	}

	private mounted(): void {
		this.listenForBookings()
	}

	/**
	 * Listen Upcoming bookings
	 */

	private listenForBookings() {
		this.getBookings()
		.then(() => {
			this.listenBooking = window.setTimeout(() => this.listenForBookings(), this.listenInterval)
		})
	}

	private getBookings(): Promise<void> {
		return Portal.client.call.booking.get()
			.response
			.then(result => {
				this.bookings = result.filter(el => !el.IsCancelled && !el.IsUsed && new Date(el.TimeSlot.End).getTime() > (Date.now() + this.clientTimeDelta))
				if (this.bookings.length) {
					this.bookings = this.bookings.sort((a, b) => a.TimeSlot.Start > b.TimeSlot.Start ? 1 : -1)
					this.checkNextBooking()
				}
			})
	}

	// Check next upcoming booking
	private checkNextBooking() {
		const interval = new Date(this.bookings[0].TimeSlot.Start).getTime() - (Date.now() + this.clientTimeDelta)
		if (interval < 120 * 1000)
			this.$emit("timeUntilBooking", this.bookings[0])
	}

	/**
	 * Time until booking
	 */

	private timeUntilBooking(booking: IBooking) {
		this.$emit("timeUntilBooking", booking)
	}

	private getTime(time: string) {
		const d = this.getDayLabel(time)
		const h = new Date(time).getHours()
		const m = new Date(time).getMinutes()
		return `${d} ${this.amPm(h, m)}`
	}

	/**
	 * @returns day label i.e. Today, Tomorrow, Friday...
	 */

	private getDayLabel(day: string): string {
		const date = new Date(day)
		const today = new Date()
		const tomorrow = new Date()
		tomorrow.setDate(today.getDate() + 1)

		if (date.getDate() === today.getDate())
			return i18n.t("today").toString()
		else if (date.getDate() === tomorrow.getDate())
			return i18n.t("tomorrow").toString()
		return `${this.daysInWeek[new Date(day).getDay()]} ${new Date(day).getMonth() + 1}/${new Date(day).getDate()}`
	}

	/**
	 * Convert hour in am/pm format
	 */

	private amPm(hour: number, min: number): string {
		const m = (min && min < 10) ? this.leadZero(min) : min
		if (hour > 12)
			return `${hour % 12}${m ? ":" + m : ""}pm`
		else if (hour < 12)
			return `${hour}${m ? ":" + m : ""}am`
		return `12${m ? ":" + m : ""}pm`
	}

	/**
	 * Lead zero
	 */

	private leadZero = (x: number): string => x < 10 ? x < 0 ? `00` :	`0${x}` : `${x}`
}
